<template>
	<ul>
		<el-card :class="item.done ? 'class-b':'class-a'">
      <el-checkbox v-model="item.done"></el-checkbox> 
        <li>{{item.title}}</li>
        <slot></slot>
        <el-button 
          type="danger" 
          icon="el-icon-delete" 
          circle 
          @click="removeto(item)"
          class="addbtns">
        </el-button>  
      </el-card>  
	</ul>   
</template>

<script>
	export default{
		name:"sas",
		props:{
			item:{
				type:Object,
				required:true
			}
		},
		methods:{
			removeto(){
        this.$emit("sas","item")
      },
		}
	}
</script>


