<template>
  <div class="hello">
    <h1>TODOLIST</h1>

    <el-tabs v-model="activeName" >
      <el-tab-pane label="全部任务" name="first">
        <el-row>
          <el-col :span="20">
            <el-input 
              type="text" 
              v-model="newtodotitle"
              placeholder="What needs to be done?"
              @keyup.enter.native="addto()"
              suffix-icon="el-icon-edit">
            </el-input>
          </el-col>
          <el-col :span="4">
          <el-button 
            type="primary" 
            icon="el-icon-plus" 
            @click="addto()"
            class="addbtn"
            round>
            添加任务
          </el-button>
          </el-col>
        </el-row>
        <sas 
          :item="item"  
          @sas="removeto(item)" 
          v-for="(item,id) in list" 
          :key="item.id"
        >
          <i class="el-icon-edit reset-i" @click="editTodo(item)"></i>
          <input 
            type="text" 
            value="编辑todo" 
            v-focus="true"
            v-model="item.title"
            v-if="editedTodo != null && editedTodo.id === item.id"
            @keyup.enter="editDone(item)"
            @keyup.esc="cancelEdit(item)">
          </sas>
      </el-tab-pane>
      
      <el-tab-pane label="进行中" name="second" >
        <sas 
          :item="item"  
          @sas="removeto(item)" 
          v-for="(item,id) in list" 
          :key="item.id"
          v-if="!item.done">
        </sas>
      </el-tab-pane>
      <el-tab-pane label="已完成" name="third">
        <sas 
          :item="item"  
          @sas="removeto(item)" 
          v-for="(item,id) in list" 
          :key="item.id"
          v-if="item.done">
        </sas>
        <el-button type="danger" @click="cleans()" class="reset-button">清空已完成</el-button>
      </el-tab-pane>
    </el-tabs>
    <p>{{allNum}}全部任务</p>
    <p>{{noCheckNum}}进行中</p>
    <p>{{shuownum}}已完成</p>
  </div>
</template>

<script>

import sas from './as'
let id = 1
var STORAGE_KEY = 'vue2.x-todo';
var todoStorage = {
    fetch: function () {
        var list = JSON.parse(localStorage.getItem(STORAGE_KEY) || '[]');
        list.forEach(function (item, index) {
            item.id = index
        });
        todoStorage.uid = list.length;
        return list
    },
    save: function (list) {
        localStorage.setItem(STORAGE_KEY, JSON.stringify(list))
    }
};
export default {
  name: 'HelloWorld',
  components: {
    sas
  },
  data () {
    return {
       newtodotitle:"",
       list: todoStorage.fetch(),
       value: true,
       editedTodo:"",
       activeName: "first"
    }
  },
  watch: {
    list: {
        handler: function (list) {
          todoStorage.save(list)
          },
          deep: true
        }
    },
  computed:{
      noCheckNum:function(as){
        return this.list.filter(as => !as.done).length
      },
      shuownum:function(sa){
        return this.list.filter(sa => sa.done).length
      },
      allNum:function(){
        return this.list.length
      }
    },
    methods:{
      addto(){
        if(this.newtodotitle.length!= 0 && this.newtodotitle.trim() != 0){
          this.list.push(
          { 
            id: todoStorage.uid++,
            title:this.newtodotitle,
            done:false
          })
        }
        this.newtodotitle=''
      },
      editTodo(item){
        this.editedTodo = {id : item.id , title:item.title}
      },
      editDone(item){
        this.editedTodo = null
      },
      cancelEdit(item){
        item.title = this.editedTodo.title;
        this.editedTodo = null
      },
      removeto(item){
        this.$confirm('此操作会永久删除该文件，是否继续','提示',{
          confirmButtonText:'确定',
          cancelButtonTetx:'取消',
          type:'warning'
        }).then(() => {
          this.list.splice(this.list.indexOf(item),1)
          this.$message({
            type:'success',
            message:'删除成功'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      cleans(){
        this.$confirm('确定要全部清空么，是否继续','提示',{
          confirmButtonText:'确定',
          cancelButtonTetx:'取消',
          type:'warning'
        }).then(() => {
          this.list =  this.list.filter(item => !item.done)
          this.$message({
            type:'success',
            message:'删除成功'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    directives:{
        focus: {
          inserted:function(el) {
            el.focus()
          }
        }
      }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>


</style>



